@charset "UTF-8";

@import "base";

.contents.mypage{display: flex; align-items: flex-start; justify-content: space-between; padding: 60px 48px 190px;}
.contents.mypage .mypage-con{padding-left: 88px; width: calc(100% - 224px);}
.mypage-con .title .page-tit{margin: 0 0 6px; text-align: left;}
.mypage-con .title p{font-size: 16px; color: #7C828B;}

.mypage-con .mypage-sec{margin-top: 32px;}
.mypage-con .mypage-sec .sub-title{display: flex; align-items: center; justify-content: space-between; margin-bottom: 16px;}
.mypage-con .mypage-sec .sub-title h3{font-size: 24px; font-weight: 700;}
.mypage-con .mypage-sec .sub-title .util-edit{display: block; padding: 5px 16px; border: 1px solid #B9C0C6; border-radius: 24px;}

.mypage-con .mypage-sec .info-con{display: flex; align-items: center; padding: 48px 0; background-color: #fff; -webkit-border-radius: 24px;-moz-border-radius: 24px;border-radius: 24px; box-shadow: 0 3px 6px rgba(0,37,106,0.05);}
.mypage-con .mypage-sec .info-con .items{display: flex; align-items: center; flex-wrap: wrap; justify-content: center; width: 25%; height: 132px; border-right: 1px solid #EBEBEB;}
.mypage-con .mypage-sec .info-con .items:last-of-type{border-right: none;}
.mypage-con .mypage-sec .info-con .items > dt{flex-shrink: 0; width: 100%; font-size: 14px; color: #2E3337; text-align: center;}
.mypage-con .mypage-sec .info-con .items > dd{display: flex; align-items: center; justify-content: center; flex-wrap: wrap; padding: 15px 10px 0; width: 100%; height: 100%; font-size: 16px; font-weight: 700; word-break: break-all;}
.mypage-con .mypage-sec .info-con .items > dd .img-con{overflow: hidden; margin: 0 auto; width: 88px; height: 88px; -webkit-border-radius: 50%;-moz-border-radius: 50%;border-radius: 50%;}
.mypage-con .mypage-sec .info-con .items > dd .img-con img{width: 100%; height: 100%; object-fit: cover;}
.mypage-con .mypage-sec .info-con .items > dd .grade{display: flex; align-items: center; justify-content: center; flex-wrap: wrap; position: relative;}
.mypage-con .mypage-sec .info-con .items > dd .grade::before{content:""; display: none; margin-bottom: 2px; width: 100%; height: 20px; background-repeat: no-repeat; background-position: center center; background-size: 20px auto;}
.mypage-con .mypage-sec .info-con .items > dd .grade.silver::before{display: block; background-image: url("../img/common/ico_silver.svg");}
.mypage-con .mypage-sec .info-con .items > dd .grade.silver > span{font-size: 14px; font-weight: 500; color: #7C828B;}
.mypage-con .mypage-sec .info-con .items > dd .grade.gold::before{display: block; background-image: url("../img/common/ico_gold.svg");}
.mypage-con .mypage-sec .info-con .items > dd .grade.gold > span{font-size: 14px; font-weight: 500; color: #D19D54;}
.mypage-con .mypage-sec .info-con .items > dd .grade .info-user{margin-left: 4px; line-height: 1;}
.mypage-con .mypage-sec .info-con .items > dd .grade .util-edit{display: none;}

.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box{display: none; position: absolute; top: 100%; right: 0; width: 290px; background-color: #fff; border: 1px solid #D8DDE2; -webkit-border-radius: 16px;-moz-border-radius: 16px;border-radius: 16px; box-shadow: 0 3px 6px rgba(0,37,106,0.05);}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-title{display: flex; justify-content: space-between; align-items: center; padding: 12px 16px; border-bottom: 1px solid #D8DDE2;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-title p{font-size: 14px; font-weight: 500;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-title button{display: block; width: 16px; line-height: 1;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-con{padding: 12px 16px;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-con dl{text-align: left; font-size: 12px;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-con dl + dl{margin-top: 10px;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-con dl dt{margin-bottom: 2px; font-weight: 500;}
.mypage-con .mypage-sec .info-con .items > dd .grade .tooltip-box .tooltip-con dl dd{color: #7C828B; font-weight: 400;}

.mypage-con .mypage-sec .activity-con{display: flex; justify-content: space-between;}
.mypage-con .mypage-sec .activity-con .items{padding: 32px 0; width: calc(33.33% - 16px); text-align: center; background-color: #fff; -webkit-border-radius: 24px;-moz-border-radius: 24px;border-radius: 24px; box-shadow: 0 3px 6px rgba(0,37,106,0.05);}
.mypage-con .mypage-sec .activity-con .items::before{content:""; display: block; margin: 0 auto; width: 92px; height: 92px; background-position: center center; background-repeat: no-repeat; background-size: 100% auto;}
.mypage-con .mypage-sec .activity-con .items:nth-of-type(1)::before{background-image: url("../img/common/ico_activity_01.png");}
.mypage-con .mypage-sec .activity-con .items:nth-of-type(2)::before{background-image: url("../img/common/ico_activity_02.png");}
.mypage-con .mypage-sec .activity-con .items:nth-of-type(3)::before{background-image: url("../img/common/ico_activity_03.png");}
.mypage-con .mypage-sec .activity-con .items dl{margin: 20px 0 16px; text-align: center;}
.mypage-con .mypage-sec .activity-con .items dl dt{font-size: 14px; color: #2E3337;}
.mypage-con .mypage-sec .activity-con .items dl dd{font-size: 48px; font-weight: 700;}
.mypage-con .mypage-sec .activity-con .items .util-edit{display: inline-block; padding: 5px 16px; border: 1px solid #B9C0C6; border-radius: 24px;}

.mypage-con .mypage-sec .career-con{background-color: #fff; -webkit-border-radius: 24px;-moz-border-radius: 24px;border-radius: 24px; box-shadow: 0 3px 6px rgba(0,37,106,0.05);}
.mypage-con .mypage-sec .career-con .nodata{padding: 40px 0; text-align: center;}
.mypage-con .mypage-sec .career-con .nodata img{margin: 0 auto; width: 78px;}
.mypage-con .mypage-sec .career-con .nodata p{margin-top: 12px; font-size: 16px; color: #7C828B;}
.mypage-con .mypage-sec .career-table{}
.mypage-con .mypage-sec .career-table .head{padding: 32px 32px 20px; border-bottom: 1px solid #D8DDE2;}
.mypage-con .mypage-sec .career-table .head ul,
.mypage-con .mypage-sec .career-table .career-list ul{display: flex;}
.mypage-con .mypage-sec .career-table .head ul li:nth-child(1),
.mypage-con .mypage-sec .career-table .career-list ul li:nth-child(1){width: 10%;}
.mypage-con .mypage-sec .career-table .head ul li:nth-child(2),
.mypage-con .mypage-sec .career-table .career-list ul li:nth-child(2){width: 63%;}
.mypage-con .mypage-sec .career-table .head ul li:nth-child(3),
.mypage-con .mypage-sec .career-table .career-list ul li:nth-child(3){width: 12%;}
.mypage-con .mypage-sec .career-table .head ul li:nth-child(4),
.mypage-con .mypage-sec .career-table .career-list ul li:nth-child(4){width: 15%;}

.mypage-con .mypage-sec .career-table .head ul li{font-size: 14px; color: #7C828B; text-align: center;}
.mypage-con .mypage-sec .career-table .career-list{padding: 20px 32px 32px;}
.mypage-con .mypage-sec .career-table .career-list ul{height: 48px;}
.mypage-con .mypage-sec .career-table .career-list ul + ul{margin-top: 6px;}
.mypage-con .mypage-sec .career-table .career-list ul li{display: flex; align-items: center; justify-content: center; text-align: center; font-weight: 500;}
.mypage-con .mypage-sec .career-table .career-list ul li:nth-of-type(2){text-align: left; justify-content: flex-start;}
.mypage-con .mypage-sec .career-table .career-list ul li .tag{font-size: 14px; color: #7C828B;}
.mypage-con .mypage-sec .career-table .career-list ul li .tag.gold{color: #D19D54;}
.mypage-con .mypage-sec .career-table .career-list ul li .title{overflow: hidden; display: block; padding-right: 10px; width: 100%; color: #2E3337; white-space: nowrap; text-overflow: ellipsis;}
.mypage-con .mypage-sec .career-table .career-list ul li .stat{display: block; margin: 0 auto; width: 72px; height: 32px; line-height: 32px; font-size: 14px; -webkit-border-radius: 6px;-moz-border-radius: 6px;border-radius: 6px;}
.mypage-con .mypage-sec .career-table .career-list ul li .stat-1{color: #7C828B; background-color: #F4F5F8;}
.mypage-con .mypage-sec .career-table .career-list ul li .stat-2{color: #18AA88; background-color: #E6FFF3;}
.mypage-con .mypage-sec .career-table .career-list ul li .stat-3{color: #3575F6; background-color: #F5F8FF;}
.mypage-con .mypage-sec .career-table .career-list ul li .stat-4{color: #EA463D; background-color: #FAE9E7;}
.mypage-con .mypage-sec .career-table .career-list ul li .cash{font-size: 14px; font-weight: 500; color: #B9C0C6;}
.mypage-con .mypage-sec .career-table .career-list ul li .cash.apply{display: flex; align-items: center; justify-content: center; color: #3575F6;}
.mypage-con .mypage-sec .career-table .career-list ul li .cash.apply::before{content:""; display: block; margin-right: 4px; width: 16px; height: 16px; background: url("../img/common/ico_pay_blue.svg") no-repeat center center / 100% auto;}
.mypage-con .mypage-sec .career-table .career-list ul li .cash.done{display: flex; align-items: center; justify-content: center; color: #B9C0C6;}
.mypage-con .mypage-sec .career-table .career-list ul li .cash.done::before{content:""; display: block; margin-right: 4px; width: 16px; height: 16px; background: url("../img/common/ico_pay_gray.svg") no-repeat center center / 100% auto;}

@media all and (max-width:1200px){
    .contents.mypage .mypage-con{padding-left: 40px; width: calc(100% - 180px);}

    .mypage-con .mypage-sec .career-table .career-list ul li .tag{font-size: 12px;}
    .mypage-con .mypage-sec .career-table .career-list ul li .title{font-size: 14px;}
    .mypage-con .mypage-sec .career-table .career-list ul li .stat{font-size: 12px;}
    .mypage-con .mypage-sec .career-table .career-list ul li .cash{font-size: 12px;}
}

@media all and (max-width:960px){
    .contents.mypage{display: block; padding: 0; background-color: #F4F5F8;}
    .contents.mypage .mypage-con{padding: 0 48px 32px; width: 100%;}
    .contents.mypage .mypage-con.sub-link{padding-top: 34px;}
}

